<!--  -->
<template>
  <div class="body">
    <el-dialog :title="dialog.title" :close-on-click-modal="false" :visible.sync="dialog.open">
      <el-form :model="subject" ref="subjectForm" :rules="rules" label-width="100px" class="edit-form">
        <el-form-item label="id" class="hidden" prop="id">
          <el-input type="hidden" v-model="subject.id"/>
        </el-form-item>
        <el-form-item label="" prop="picture">
          <sui-photo type="subject" v-model="subject.picture" :disabled="dialog.disabled"/>
        </el-form-item>
        <el-form-item label="" prop="title">
          <el-input v-model="subject.title" placeholder="" :disabled="dialog.disabled" class="sui-input"/>
        </el-form-item>
        <el-form-item label="" prop="name">
          <el-input v-model="subject.name" placeholder="" :disabled="dialog.disabled" class="sui-input"/>
        </el-form-item>
        <el-form-item label="" prop="categoryId">
          <el-select v-model="subject.categoryId" placeholder="" :disabled="dialog.disabled">
            <el-option v-for="category in categoryDict" :key="category.id" :value="category.id" :label="category.name"/>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="content">
          <el-input type="textarea" v-model="subject.content" placeholder="" :disabled="dialog.disabled"/>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button size="small" @click="cancel"> </el-button>
        <el-button size="small" v-if="!dialog.disabled" type="primary" @click="submitForm()"> </el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
  export default {
    name: "subjectForm",
    data() {
      return {
        categoryDict:[],
        //
        dialog: {
          title: "",
          open: false,
          disabled: false
        },
        //
        subject: {
          picture: "",
          title: "",
          name: "",
          categoryId: "",
          content: ""
        },
        //
        rules: {
          picture: [{required: true, message: '', trigger: 'blur'}],
          title: [{required: true, message: '', trigger: 'blur'},{validator:this.$validator.unique,form:this,model:'subject',tableName:'subject',column:'title',message:'',trigger:'blur'}],
          name: [{required: true, message: '', trigger: 'blur'}],
          categoryId: [{required: true, message: '', trigger: 'blur'}],
          content: [{required: true, message: '', trigger: 'blur'}]
        }
      }
    },
    computed: {
    },
    methods: {
      //
      getCategoryDict() {
        this.$request.doGet('/admin/category/getList').then(res => {
          if (res.isOk) {
            this.categoryDict = res.list;
          }
        })
      },
      //
      open(option) {
        const pageFrom = option.pageFrom;
        const disabled = pageFrom == 'view' ? true : false;
        this.showDialog(option.title, disabled);
        this.getCategoryDict();//
        if (pageFrom == 'edit' || pageFrom == 'view') {
          this.getSubject(option.id);
        }
      },
      //
      cancel: function () {
        this.dialog.open = false;
      },
      //id
      getSubject(id) {
        this.$request.doGet("/admin/subject/get?id=" + id).then(res => {
          if (res.isOk) {
            this.subject = res.obj;
          }
        });
      },
      //
      submitForm: function () {
        const that = this;
        that.$refs.subjectForm.validate((valid) => {
          if (valid) {//
            that.$request.doPost('/admin/subject/sub', that.subject).then(res => {
              if (res.isOk) {
                that.cancel();
                that.showSuccess("");
                that.$emit('reloadList');
              }
            });
          }
        });
      },
      //
      showDialog(title, disabled) {
        this.resetForm(this.$refs.subjectForm);//
        this.dialog.open = true;//
        this.dialog.title = title;//
        this.dialog.disabled = disabled;//
      }
    }
  }
</script>

<style scoped>

</style>



